/* topdoc
  name: Less variables
  family: cf-icons
  notes:
    - "These variables can be overriden in your project's Less file."
  patterns:
    - name: CSS prefix
      codenotes:
        - "@cf-icon-prefix: cf-icon;"
    - name: Path to icon font files
      codenotes:
        - "@cf-icon-path: '../fonts';"
    - name: Toggle IE7 support
      codenotes:
        - "@cf-icon-ie7-support: true;"
      notes:
        - "More on IE7 support can be found in the 'IE7 Support' section."
    - name: Icon border color
      codenotes:
        - "@cf-border-color: #eee;"
      notes:
        - "Used in .cf-icon__border"
  tags:
    - cf-icons
*/

@cf-icon-prefix:                cf-icon;
@cf-icon-path:                  'fonts';
@cf-icon-ie7-support:           true;
@cf-icon-border-color:          #eee;

/* topdoc
  name: IE7 Support
  family: cf-icons
  notes:
    - "IE7 support is added by using dynamic properties (CSS expressions)."
    - "To turn off IE7 support simply set @cf-icon-ie7-support to false."
  tags:
    - cf-icons
*/

.cf-icon-ie7(@inner) when (@cf-icon-ie7-support) {
    *zoom: ~"expression( this.runtimeStyle['zoom'] = '1', this.innerHTML = '@{inner}')";
}


/* topdoc
  name: The basics
  family: cf-icons
  patterns:
    - name: "@font-face declaration"
    - name: Base icon class
      notes:
        - "The cf-icon-prefix class applies all shared icon styles including the
          font family."
        - "All icons must use two classes, one for this base class and another
          to set the font character. For example:
          <span class='cf-icon cf-icon-left'></span>."
        - "Please use the span element instead of the i element. This avoids
          font family cascading conflicts when using an italic webfont on i
          elements and then another font for the icons. Note that this issue
          only pops up in older versions of Internet Explorer."
  tags:
    - cf-icons
*/

@font-face {
  font-family: 'CFPB Minicons';
  src: url('@{cf-icon-path}/cf-icons.eot');
  src: url('@{cf-icon-path}/cf-icons.eot?#iefix') format('embedded-opentype'),
    url('@{cf-icon-path}/cf-icons.woff') format('woff'),
    url('@{cf-icon-path}/cf-icons.ttf') format('truetype'),
    url('@{cf-icon-path}/cf-icons.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}

// The class .cf-icon is used to support adding these rules via a mixin,
// specifically when @cf-icon-prefix is changed to something other than
// 'cf-icon', since string interpolation is incompatible with mixin calls.
//
// For example you can't do this:
// .link-with-auto-icon {
//   .@{cf-icon-prefix}();
// }
//
// But you can do this:
// .link-with-auto-icon {
//   .@cf-icon();
// }
.cf-icon,
.@{cf-icon-prefix} {
  font-family: 'CFPB Minicons';
  display: inline-block;
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
}


